<template>
  <div id="app">
    <div class="card1">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="电厂名称">
          <el-input v-model="form.name1"></el-input>
        </el-form-item>
        <el-form-item label="电厂类型">
          <el-select v-model="value0" placeholder="请选择">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item lable-width="120px" label="发电机组容量" width='120'>
          <el-select v-model="value1" placeholder="请选择">
            <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="升压站电压等级">
          <el-select v-model="value2" placeholder="请选择">
            <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所在省份">
          <el-select v-model="value3" placeholder="请选择">
            <el-option v-for="item in options3" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="电厂地址">
          <el-input v-model="form.name2"></el-input>
        </el-form-item>
        <el-form-item label="电话">
          <el-input v-model="form.name3"></el-input>
        </el-form-item>
        <el-form-item label="邮编">
          <el-input v-model="form.name4"></el-input>
        </el-form-item>
        <el-button type="primary" @click='dataPush'>保存 </el-button>
      </el-form>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'xinjian',
    data() {
      return {
        options: [{
          value: '水电厂'
        }, {
          value: '火电厂'
        }, {
          value: '风电厂'
        }, {
          value: '气电厂'
        }],
        options1: [{
          value: '10L'
        }, {
          value: '20L'
        }, {
          value: '30L'
        }, {
          value: '40L'
        }],
        options2: [{
          value: '特级'
        }, {

          value: '一级'
        }, {

          value: '二级'
        }, {

          value: '三级'
        }],
        options3: [{

          value: '陕西省'
        }, {

          value: '山西省'
        }, {

          value: '山东省'
        }, {

          value: '湖北省'
        }],
        value0: '',
        value1: '',
        value2: '',
        value3: '',
        form: {
          name1: '',
          name2: '',
          name3: '',
          name4: '',
        },

      }
    },
    methods: {
      dataPush() {
        if (!(this.form.name1 && this.form.name2 && this.form.name3 && this.form.name4 && this.value0 && this.value1 &&
            this.value2 && this.value3)) {
          alert('请检查你输入的数据')
        } else {
          var data = {
            name1: this.form.name1,
            name2: this.form.name2,
            name3: this.form.name3,
            name4: this.form.name4,
            value0: this.value0,
            value1: this.value1,
            value2: this.value2,
            value3: this.value3,
          }
          localStorage.setItem('data', JSON.stringify(data))
          this.$router.push({
            path: './dianchangguanli',

          })
        }
      }
    }
  }

</script>

<style lang="" scoped>
  .card1 {
    width: 550px;
    padding: 10px;
    margin: 0 auto;
    text-align: center;
    background: white;
  }

  .el-form-item__label {
    width: 100px !important;
    padding: 0 !important;
  }

  .card1 .el-input {
    width: 400px;
    height: 30px;
  }

  .card1 .el-select {
    width: 400px;
    height: 25px;
  }


  .card1 .el-button {
    padding: 8px 16px;
  }

  .element.style {
    width: 120px;
  }

</style>



